import React, { Component } from 'react'
import EditableList from '@/components/EditableList'
import UserSearchBox from '@/components/SearchBox/UserSearchBox'
import NerdIcon from '@/icons/nerd.svg'
import compareBy from '@/utilities/compareBy'
import './Participants.scss'

const addParticipants = (map, users, addition) => {
  users.forEach(user => {
    map.set(user._id, {
      ...(map.get(user._id) || user),
      ...addition,
    })
  })
}

export default class Participants extends Component {
  static defaultProps = {
    className: '',
    editors: [],
    owners: [],
    players: [],
  }

  static getDerivedStateFromProps(props, state) {
    const { editors, owners, players } = props
    const map = new Map()
    addParticipants(map, players, { isPlayer: true, level: 'player', order: 3 })
    addParticipants(map, editors, { isEditor: true, level: 'editor', order: 2 })
    addParticipants(map, owners, { isOwner: true, level: 'owner', order: 1 })

    const participants = Array.from(map.values()).sort(compareBy('order'))
    return { ...state, participants }
  }

  state = {
    participants: [],
  }

  render = () => {
    const { className } = this.props
    const { participants } = this.state

    return (
      <div className={`participants ${className}`}>
        <ul className="participant-list">
          {participants.map(user => (
            <li className="row" key={user._id}>
              <NerdIcon />
              <div className="display">
                <span className="name">{user.name}</span>
                <span className="username">{user.username}</span>
              </div>
              <EditableList
                className="role"
                options={['player', 'editor', 'owner']}
                defaultValue={user.level}
              />
            </li>
          ))}
        </ul>
        <UserSearchBox className="add-user" />
      </div>
    )
  }
}
